* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
body {
  background-color: rgb(103, 14, 187);
}
main {
  padding: 15px;
}
.container {
  max-width: 900px;
  margin: auto;
  margin-top: 50px;
  text-align: center;
  display: flex;
  padding: 15px;
  background-color: white;
  /* justify-content: space-between; */
  border-radius: 5px;
  box-shadow: white 2px 2px 10px;
}
.header {
  width: 30%;
  margin-top: 15px;
  text-align: left;
}
.questions {
  width: 70%;
  padding: 10px;
}
.singleQuestion {
  width: 100%;
  margin-top: 15px;
  border-radius: 5px;
  padding: 10px;
  font-size: 12px;
  box-shadow: rgb(58, 56, 56) 1px 1px 3px;
}
.questiontitle {
  display: flex;
  justify-content: space-between;
}
.btn {
  border-color: transparent;
  width: 25px;
  height: 25px;
  color: rgb(250, 250, 250);
  background-color: rgb(125, 53, 207);
  border-radius: 50%;
  outline-color: rgb(193, 171, 218);
  outline-width: 25px;
  position: relative;
}

.btn:hover {
  color: rgb(231, 230, 231);
  background-color: rgba(64, 59, 68, 0.986);
}

.info {
  padding: 5px;
  margin-top: 10px;
  font-size: 16px;
}

/*  responsive  */

@media only screen and (max-width: 650px) {
  .container {
    display: block;
  }
  .header {
    width: 100%;
    font-size: 18px;
  }
  .questions {
    width: 100%;
    padding: 10px;
  }
  h1 {
    font-size: 25px;
    padding: 15px;
  }
}

@media only screen and (max-width: 300px) {
  .container {
    display: block;
    text-align: left;
    padding: 5px;
  }
  .header {
    width: 100%;
  }
  .questions {
    width: 100%;
    padding: 10px;
  }
  h1 {
    font-size: 25px;
    padding: 15px;
  }
  main {
    padding: 5px;
  }
}
